import { NotFound } from 'http-errors'

export async function errorHandler(ctx, next) {
  try {
    await next()
  } catch (err) {
    console.error(err)
    ctx.status = err.status || 500
    ctx.body = [{ message: err.message }]
  }
}

export async function notFound(ctx) {
  throw new NotFound()
}



